import { derived, readable } from 'svelte/store';
const clientIdStorageKey = 'lesocki-client-id';
let socket;
export function send(data) {
    if (socket) {
        console.log('SEND: ', data);
        socket.send(JSON.stringify(data));
    }
}
export const gameReport = readable(null, (set) => {
    const url = window.location.hostname === 'localhost'
        ? `ws://localhost:3000/ws`
        : `wss://${window.location.host}/ws`;
    socket = new WebSocket(url);
    socket.addEventListener('message', (event) => {
        const report = JSON.parse(event.data);
        console.log('Report: ', report);
        set(report);
        window.sessionStorage.setItem(clientIdStorageKey, report.myClientId);
    });
    socket.addEventListener('open', () => {
        send({
            command: 'connect',
            clientId: window.sessionStorage.getItem(clientIdStorageKey) || null
        });
    });
    return function stop() {
        console.log('DISCONNECT');
        socket.close();
    };
});
export const me = derived(gameReport, $r => $r.myIndex >= 0 ? $r.players[$r.myIndex] : null);
